<template>
    <section>
        <h3>개인회원가입</h3>
        <div>
            <label for="user_id">id: </label>
            <input v-model="user_id" required type="text" placeholder="아이디를 입력하세요">
        </div>
        <div>
            <label for="user_pw">password: </label>
            <input v-model="user_pw" required type="password" placeholder="비밀번호를 입력하세요">
        </div>
        <div>
            <label for="user_name">이름: </label>
            <input v-model="user_name" required type="text" placeholder="이름 입력하세요">
        </div>
        <div>
            <label for="user_birth">생일: <input v-model="user_birth" required type="text" placeholder="생일 입력하세요"></label>
        </div>

        <div>
            <label for="user_email">이메일: </label>
            <input v-model="user_email" required type="text" placeholder="이메일 입력하세요">
        </div>

        <div>
            <label for="user_hp">전화번호: </label>
            <input v-model="user_hp" required type="text" placeholder="전화번호를 입력하세요">
        </div>

        <div>
            <label for="user_gender">성별: </label>
            <input v-model="user_gender" required type="text">
        </div>
        <div>
            <!-- <label for="com_address">회사주소: </label> -->
            <!-- <input v-model="com_address" required type="text" placeholder="회사주소를 입력하세요"> -->
            <input type="text" v-model="user_address" placeholder="주소"><br>
        </div>

        <div class="list">
            <button type="submit" @click="submitForm()">가입하기</button>
        </div>
    </section>
    <h4>푸터</h4>
</template>
  
  
<script>
export default {
    data() {
        return {
            com_name: '',
            com_ceo: '',
            business_number: '',
            postcode: "",
            address: "",
            extraAddress: "",
            com_userid: '',
            com_password: '',
            com_number: '',
            com_manager: '',
            com_email: '',
        };
    }, methods: {
        btn_view() {
            location.href = "page1";
        },
        execDaumPostcode() {
            new window.daum.Postcode({
                oncomplete: (data) => {
                    if (this.extraAddress !== "") {
                        this.extraAddress = "";
                    }
                    if (data.userSelectedType === "R") {
                        // 사용자가 도로명 주소를 선택했을 경우
                        this.address = data.roadAddress;
                    } else {
                        // 사용자가 지번 주소를 선택했을 경우(J)
                        this.address = data.jibunAddress;
                    }

                    // 사용자가 선택한 주소가 도로명 타입일때 참고항목을 조합한다.
                    if (data.userSelectedType === "R") {
                        // 법정동명이 있을 경우 추가한다. (법정리는 제외)
                        // 법정동의 경우 마지막 문자가 "동/로/가"로 끝난다.
                        if (data.bname !== "" && /[동|로|가]$/g.test(data.bname)) {
                            this.extraAddress += data.bname;
                        }
                        // 건물명이 있고, 공동주택일 경우 추가한다.
                        if (data.buildingName !== "" && data.apartment === "Y") {
                            this.extraAddress +=
                                this.extraAddress !== ""
                                    ? `, ${data.buildingName}`
                                    : data.buildingName;
                        }
                        // 표시할 참고항목이 있을 경우, 괄호까지 추가한 최종 문자열을 만든다.
                        if (this.extraAddress !== "") {
                            this.extraAddress = `(${this.extraAddress})`;
                        }
                    } else {
                        this.extraAddress = "";
                    }
                    // 우편번호를 입력한다.
                    this.postcode = data.zonecode;
                },
            }).open();


        },
        
        submitForm() {
            this.$axios.post('/test/registForm',
                {
                    user_id: this.member.user_id,
                    user_pw: this.member.user_pw,
                    user_name: this.member.user_name,
                    user_birth: this.member.user_birth,
                    user_email: this.member.user_email,
                    user_hp: this.member.user_hp,
                    user_status: this.member.user_status,
                    user_gender: this.member.user_gender,
                    user_address: this.member.user_address
                }
            ).then(res => {
                console.log(res)
            }).catch(err => {
                console.log(err)
            })
        }
    },
};



</script>
  
<style>
form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

label {
    font-weight: bold;
}

input {
    padding: 5px;
    border-radius: 3px;
    border: 1px solid #ccc;
    width: 100%;
}

button[type="submit"] {
    padding: 10px;
    font-size: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.view {
    padding: 10px;
    font-size: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    background-color: #005fa3;
}

.list {
    text-align: center;
    margin-top: 30px;
}
</style>
  
  